                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 Enfoque de mujeres campesinas y sus derechos
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 Enfoque de orientaciones sexuales e identidades de gnero diversas
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 Enfoque etario
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 Enfoque de discapacidad
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 Enfoque de personas cuidadoras
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 Enfoque ambiental
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 Enfoque de soberana, seguridad y autonoma alimentaria

        .titulo-con-imagen.mb-5(data-aos="fade-right")
          .titulo-con-imagen__fondo
            img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
            h3.titulo-con-imagen__texto Temas clave que regula el Decreto 1004 de 2024
        
        p.mb-5 A continuacin, se presenta un pdcast que analiza los principales temas regulados por el Decreto 1004 de 2024, destacando los mecanismos de representacin campesina, el funcionamiento de la Comisin Mixta y las disposiciones que fortalecen la interlocucin entre el Estado y las comunidades rurales.

        .row.mb-5.justify-content-center.align-items-center
          .col-lg-3.mb-3.mb-lg-0
            figure
              img(src="@/assets/curso/tema3/59.png", data-aos="zoom-in")
          .col-lg-7
            .bg-color-1.tarjeta
              .p-4
                .row.justify-content-center.align-items-center
                  .col-lg-5.mb-3.mb-lg-0
                    figure
                      img(src="@/assets/curso/tema3/60.svg", data-aos="zoom-in")
                  .col-lg-7
                    TarjetaAudio.color-acento-contenido.bg-color-white.mb-3(
                      texto="Proceso de descarga de MySQL Server Community y MySQL Workbench"
                      tiempo
                      :audio="require('../assets/curso/podcast/podcast1.mp3')"
                    ) 

        .bg-full-width.bg-color-4.mb-5
          .px-4.px-md-5.py-4
            .row.align-items-center
              .col-lg-auto.mb-3.mb-lg-0
                img(src="@/assets/curso/tema3/61.svg", style="max-width: 90px").mx-auto
              .col-lg
                p.mb-0 Este decreto constituye un avance significativo en el reconocimiento formal de los espacios de participacin poltica del campesinado en el diseo, seguimiento y evaluacin de las polticas pblicas que afectan su vida y su territorio. La creacin de esta Comisin no solo fortalece la institucionalidad rural, sino que ampla la democracia desde el campo, promoviendo la equidad, el dilogo y la construccin colectiva de pas.   
    
    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Decreto 780 del 2024
    
    .row.mb-5.align-items-center
      .col-lg-4.order-lg-2.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/62.svg", data-aos="zoom-in")
      .col-lg-8.order-lg-1
        p(data-aos="fade-left") Este decreto tiene como objeto reglamentar el procedimiento mediante el cual se constituye, reconoce y fortalece la territorialidad campesina a travs de la figura de los Territorios Campesinos Agroalimentarios (TECAM). Su implementacin busca avanzar en el reconocimiento de los derechos del campesinado y en la consolidacin de formas autnomas de ordenamiento territorial en el campo colombiano. La Agencia Nacional de Tierras (ANT) ser la entidad competente para constituir, reconocer y formalizar los TECAM, a partir de una propuesta de delimitacin presentada por las comunidades campesinas organizadas. Para ello, el decreto establece una serie de conceptos fundamentales que permiten entender la naturaleza y el alcance de esta figura territorial. A continuacin, se presentan las definiciones esenciales que fundamentan el desarrollo de los TECAM:
    
    .row.mb-5
      .col-lg-4.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/63.png", data-aos="zoom-in")
      .col-lg-8
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-color-4")
          div(titulo="Territorialidades campesinas").ms-5
            p.mb-0 Son espacios que reflejan el vnculo entre el campesinado y el territorio. Incluyen dimensiones geogrficas, demogrficas, organizativas y culturales, habitadas y gestionadas histricamente por comunidades campesinas. Representan el mbito de sus actividades econmicas, sociales, culturales, polticas y ambientales, desde la perspectiva de sujetos de derechos y de especial proteccin constitucional.
          div(titulo="Territorios Campesinos Agroalimentarios (TECAM)").ms-5
            p Son territorios delimitados, organizados e histricamente habitados por familias, comunidades y organizaciones campesinas. Su propsito es garantizar la permanencia en el territorio, conservar bienes comunes de la naturaleza, promover una vida digna, fortalecer la soberana alimentaria, impulsar la agroecologa y proteger integralmente al campesinado. Constituyen un modelo de ordenamiento social y productivo rural, priorizando la reforma agraria, el desarrollo rural y la distribucin equitativa de la tierra, en el marco de la normativa vigente. 
            p.mb-0 <b>Importante</b>: los TECAM no constituyen ttulo colectivo de propiedad ni afectan el derecho a la propiedad privada o de terceros sobre predios ubicados en dichas reas.
          div(titulo="Plan de Vida Digna").ms-5
            p.mb-0 Instrumento mediante el cual las comunidades campesinas de los TECAM planifican y ordenan su territorio. Busca garantizar la permanencia, promover la soberana alimentaria, proteger el patrimonio cultural y ambiental, fortalecer la cultura campesina y resolver conflictos rurales. Proyecta acciones en el corto, mediano y largo plazo, vinculando procesos de Reforma Agraria, Reforma Rural Integral y desarrollo rural sostenible.
          div(titulo="Formas organizativas campesinas").ms-5
            p.mb-0 Agrupan asociaciones, juntas de accin comunal, juntas campesinas, familias u otras formas comunitarias de organizacin social. Estas estructuras, con autonoma y participacin, tienen la facultad de impulsar, ordenar, regular y representar al TECAM en su territorio, siempre enmarcadas en el Plan de Vida Digna y sin contradecir las funciones legales de las autoridades estatales.
    
    .bg-full-width.bg-color-1.mb-5
      .px-4.px-md-5.py-4
        .row.align-items-center
          .col-lg-auto.mb-3.mb-lg-0
            img(src="@/assets/curso/tema3/64.svg", style="max-width: 90px").mx-auto
          .col-lg
            p.mb-0 Este decreto representa un paso significativo hacia la consolidacin de un modelo de gestin territorial campesino, fundamentado en la participacin comunitaria, el enfoque agroalimentario, la defensa del ambiente y la proteccin de los derechos del campesinado como sujeto colectivo.
    
    .bg-full-width.bg-color-10
      .px-4.px-md-5.pb-md-3
        .titulo-con-imagen.mb-5(data-aos="fade-right")
          .titulo-con-imagen__fondo
            img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
            h3.titulo-con-imagen__texto Decreto 1406 del 2023
        
        .row.mb-5
          .col-lg-4.order-lg-2.mb-3.mb-lg-0
            figure
              img(src="@/assets/curso/tema3/65.png", data-aos="zoom-in")
          .col-lg-8.order-lg-1
            p(data-aos="fade-left") Este decreto establece, en su artculo primero, la conformacin de los rganos Sectoriales de Asesora y Coordinacin del Sector Administrativo Agropecuario, Pesquero y de Desarrollo Rural, los cuales son instancias clave para la formulacin y articulacin de polticas pblicas en el campo colombiano. Estos rganos son los siguientes:
        
            .bg-color-15.tarjeta.p-4.mb-3(data-aos="fade-left")
              .row.align-items-center
                .col-lg-auto.mb-3.mb-lg-0
                  img(src="@/assets/curso/tema3/66.svg", style="max-width: 270px").mx-auto
                .col-lg
                  div.bg-white.tarjeta.p-4
                    ul.lista-ul--color
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 La Comisin Nacional de Crdito Agropecuario.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 La Comisin Intersectorial para la Reforma Agraria, el Desarrollo Rural y la Reforma Rural Integral.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 El Consejo Nacional de Secretaras de Agricultura.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 El Comit Asesor de Poltica Forestal.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 El Consejo Nacional de Adecuacin de Tierras.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 La Comisin Nacional de Territorios Indgenas.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 El Consejo Asesor de Mercados Mayoristas.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 La Comisin Nacional de Insumos Agropecuarios.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 La Mesa Nacional de Insumos Agropecuarios.
        
        .row.mb-5
          .col-lg-4.mb-3.mb-lg-0
            figure
              img(src="@/assets/curso/tema3/67.png", data-aos="zoom-in")
          .col-lg-8
            p(data-aos="fade-left") En el artculo segundo, el decreto reglamenta la organizacin y funcionamiento del Sistema Nacional de Reforma Agraria y Desarrollo Rural (SINRADR), definindolo como el mecanismo obligatorio para la planeacin, coordinacin, ejecucin, evaluacin y seguimiento de las actividades dirigidas a la materializacin de la Reforma Agraria, la Reforma Rural Integral y los objetivos establecidos en la Ley 160 de 1994, as como en el Acuerdo Final de Paz de 2016.
            p El alcance del SINRADR se extiende a todas las entidades y organismos pblicos cuya misionalidad est orientada a:
        
            .bg-color-9.tarjeta.p-4.mb-3(data-aos="fade-left")
              .row.align-items-center
                .col-lg-auto.mb-3.mb-lg-0
                  img(src="@/assets/curso/tema3/68.svg", style="max-width: 270px").mx-auto
                .col-lg
                  div.bg-white.tarjeta.p-4
                    ul.lista-ul--color
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 Mejorar la calidad de vida de las poblaciones rurales.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 Garantizar los derechos territoriales y planes de vida de trabajadores agrarios, comunidades campesinas, pescadoras, ribereas y anfibias, comunidades negras, afrocolombianas, raizales, palenqueras, pueblos indgenas y el pueblo Rom.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 Proteger y fortalecer sus economas propias y sus sistemas de produccin de alimentos.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 Promover la consolidacin de una paz total con enfoque territorial, reconociendo las instancias de concertacin para el desarrollo rural y la Reforma Agraria.

        p.mb-5 A continuacin, se presenta un pdcast que describe la estructura funcional del Sistema de Reforma Agraria en Colombia, detallando los subsistemas que lo componen y su papel en la transformacin del territorio rural.

        .row.mb-5.justify-content-center.align-items-center
          .col-lg-3.mb-3.mb-lg-0.order-2
            figure
              img(src="@/assets/curso/tema3/69.png", data-aos="zoom-in")
          .col-lg-7.order-1.mb-3.mb-lg-0
            .bg-color-1.tarjeta
              .p-4
                .row.justify-content-center.align-items-center
                  .col-lg-5.mb-3.mb-lg-0
                    figure
                      img(src="@/assets/curso/tema3/60.svg", data-aos="zoom-in")
                  .col-lg-7
                    TarjetaAudio.color-acento-contenido.bg-color-white.mb-3(
                      texto="Proceso de descarga de MySQL Server Community y MySQL Workbench"
                      tiempo
                      :audio="require('../assets/curso/podcast/podcast1.mp3')"
                    ) 

        .bg-full-width.bg-color-4.mb-5
          .px-4.px-md-5.py-4
            .row.align-items-center
              .col-lg-auto.mb-3.mb-lg-0
                img(src="@/assets/curso/tema3/70.svg", style="max-width: 90px").mx-auto
              .col-lg
                p.mb-0 Este decreto representa un avance fundamental para la Reforma Agraria integral, al consolidar una estructura interinstitucional slida que reconoce la diversidad de actores rurales y la urgencia de garantizar condiciones de vida dignas, sostenibilidad productiva y justicia territorial en el campo colombiano.

    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Planes de desarrollo, ordenamiento territorial
    
    .bg-color-17.mb-5(data-aos="fade-up")
      .row.justify-content-center.align-items-center
        .col-lg-auto.mb-3.mb-lg-0.mb-3.mb-lg-0
          figure
            img(src='@/assets/curso/tema3/71.png', alt='')
        .col-lg
          .p-4
            p.mb-0(data-aos="fade-up") En Colombia, los Planes de Desarrollo (PD) y los Planes de Ordenamiento Territorial (POT) son herramientas fundamentales para planificar el desarrollo econmico, social y ambiental del pas, as como para organizar y gestionar el uso del territorio, tanto a nivel nacional como en los departamentos, distritos y municipios.

    .bg-full-width.bg-color-10
      .px-4.px-md-5.pb-md-3
        .row.mb-5
          .col-lg-4.order-lg-2.mb-3.mb-lg-0
            figure
              img(src="@/assets/curso/tema3/72.png", data-aos="zoom-in")
          .col-lg-8.order-lg-1
            p(data-aos="fade-left") El Plan de Ordenamiento Territorial (POT) es un instrumento tcnico y normativo mediante el cual se regula el uso del suelo y la ocupacin del territorio. Su enfoque es de largo plazo, con el objetivo de armonizar el desarrollo urbano y rural, proteger el medio ambiente, gestionar riesgos, y mejorar las condiciones de vida de la poblacin. El POT incluye aspectos clave como:
        
            .bg-color-1.tarjeta.p-4.mb-3(data-aos="fade-left")
              .row.align-items-center
                .col-lg-auto.mb-3.mb-lg-0
                  img(src="@/assets/curso/tema3/73.svg", style="max-width: 270px").mx-auto
                .col-lg
                  div.bg-white.tarjeta.p-4
                    ul.lista-ul--color
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 La zonificacin del territorio, es decir, la definicin de zonas urbanas, rurales, de proteccin ambiental, entre otras.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 Las normas sobre usos del suelo, indicando si una zona se puede destinar a vivienda, industria, comercio, agricultura, etc.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 La planificacin de la infraestructura y los servicios pblicos.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 La incorporacin de estrategias de gestin del riesgo y adaptacin al cambio climtico.
                      li.mb-1.d-flex
                        i.fas.fa-leaf.bg-color-7
                        p.mb-0 La proteccin del patrimonio natural y cultural del territorio.
            p Este plan debe ser formulado por las alcaldas municipales o distritales, con participacin activa de la comunidad, el Concejo Municipal, y otras entidades del nivel territorial y nacional. Su elaboracin y aplicacin estn reguladas principalmente por tres normas:

        .row.mb-5.justify-content-center
          .col-sm-6.col-lg-4.mb-4.mb-lg-0(data-aos="fade-down")
            .tarjeta--boton.bg-color-13.p-4.h-100
              .row.justify-content-center.mb-3
                .col-7
                  figure
                    img(src='@/assets/curso/tema3/74.svg', alt='Texto que describa la imagen')
              div.text-white.text-center
                h4 01
                p La Ley 388 de 1997, que establece los principios y lineamientos del ordenamiento territorial en Colombia.
          .col-sm-6.col-lg-4.mb-4.mb-lg-0(data-aos="fade-up")
            .tarjeta--boton.bg-color-13.p-4.h-100
              .row.justify-content-center.mb-3
                .col-7
                  figure
                    img(src='@/assets/curso/tema3/75.svg', alt='Texto que describa la imagen')
              div.text-white.text-center
                h4 02
                p La Ley 152 de 1994, que organiza el Sistema Nacional de Planeacin y regula la formulacin de los planes de desarrollo.
          .col-sm-6.col-lg-4.mb-4.mb-sm-0(data-aos="fade-down")
            .tarjeta--boton.bg-color-13.p-4.h-100
              .row.justify-content-center.mb-3
                .col-7
                  figure
                    img(src='@/assets/curso/tema3/76.svg', alt='Texto que describa la imagen')
              div.text-white.text-center
                h4 03
                p La Constitucin Poltica de 1991, que reconoce la autonoma territorial y promueve la planificacin como instrumento clave para el desarrollo equitativo y sostenible.

        .bg-full-width.bg-color-4.mb-5
          .px-4.px-md-5.py-4
            .row.align-items-center
              .col-lg-auto.mb-3.mb-lg-0
                img(src="@/assets/curso/tema3/77.svg", style="max-width: 90px").mx-auto
              .col-lg
                p.mb-0 En cuanto a la relacin entre el <b>Plan de Desarrollo (PD)</b> y el <b>POT</b>, es importante destacar que ambos deben estar articulados entre s. Mientras el PD establece lo que una administracin quiere lograr en un perodo de gobierno, el POT define cmo debe utilizarse y gestionarse el territorio para alcanzar esos objetivos de manera ordenada y sostenible. Una correcta armonizacin entre ambos planes asegura que las inversiones, los proyectos de infraestructura y los programas sociales se realicen teniendo en cuenta las caractersticas, necesidades y oportunidades especficas de cada territorio.
    
    .bg-full-width.bg-color-10
      .px-4.px-md-5.pb-md-3
        .titulo-con-imagen.mb-5(data-aos="fade-right")
          .titulo-con-imagen__fondo
            img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
            h3.titulo-con-imagen__texto Decreto 1987 de 2013.
        
        .row.mb-5
          .col-lg-4.order-lg-2.mb-3.mb-lg-0
            figure
              img(src="@/assets/curso/tema3/78.svg", data-aos="zoom-in")
          .col-lg-8.order-lg-1
            p(data-aos="fade-left") El Decreto 1987 de 2013 tiene como objeto organizar el Sistema de Coordinacin de actividades pblicas, privadas y de inclusin social, creado para dar cumplimiento al Pacto Nacional por el Agro y el Desarrollo Rural. Este sistema tiene como objetivo principal coordinar todos los esfuerzos de las diferentes instancias del pas, pblicas, privadas y comunitarias,  que estn relacionadas con la formulacin, ejecucin y seguimiento de polticas, programas y proyectos que beneficien al agro colombiano y al desarrollo rural en su conjunto. La articulacin del sistema se realiza en tres niveles:
        
        .row.mb-5.justify-content-center
          .col-sm-6.col-lg-4.mb-4.mb-lg-0(data-aos="fade-down")
            .tarjeta--boton.bg-color-13.p-4.h-100
              .row.justify-content-center.mb-3
                .col-7
                  figure
                    img(src='@/assets/curso/tema3/79.svg', alt='Texto que describa la imagen')
              div.text-white.text-center
                h4 Nivel municipal
                p A travs de los Consejos Municipales de Desarrollo Rural (CMDR), donde participan las autoridades locales, organizaciones campesinas y representantes de la comunidad.
          .col-sm-6.col-lg-4.mb-4.mb-lg-0(data-aos="fade-up")
            .tarjeta--boton.bg-color-13.p-4.h-100
              .row.justify-content-center.mb-3
                .col-7
                  figure
                    img(src='@/assets/curso/tema3/80.svg', alt='Texto que describa la imagen')
              div.text-white.text-center
                h4 Nivel departamental
                p Mediante los Consejos Seccionales de Desarrollo Agropecuario (Conseas), que integran los esfuerzos regionales.
          .col-sm-6.col-lg-4.mb-4.mb-sm-0(data-aos="fade-down")
            .tarjeta--boton.bg-color-13.p-4.h-100
              .row.justify-content-center.mb-3
                .col-7
                  figure
                    img(src='@/assets/curso/tema3/81.svg', alt='Texto que describa la imagen')
              div.text-white.text-center
                h4 Nivel nacional
                p A travs del Consejo Nacional Agropecuario y Agroindustrial, encargado de consolidar y alinear las polticas nacionales en torno al desarrollo rural.

        p Estas instancias tienen la tarea de generar dilogo y coordinacin entre el Estado, el sector privado y las comunidades rurales, de modo que se logre el cumplimiento efectivo del Pacto Nacional por el Agro y el Desarrollo Rural, promoviendo la inclusin social, el desarrollo productivo y el fortalecimiento institucional en el campo colombiano.
    
    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Acuerdo de Paz (2016)
    
    .row.mb-5
      .col-lg-4.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/82.png", data-aos="zoom-in")
      .col-lg-8
        p(data-aos="fade-left") Uno de los componentes ms importantes del Acuerdo Final para la Terminacin del Conflicto y la Construccin de una Paz Estable y Duradera, firmado en 2016 entre el Gobierno colombiano y las FARC, es el Punto 1, titulado "Hacia un Nuevo Campo Colombiano: Reforma Rural Integral (RRI)". Este punto busca transformar estructuralmente las condiciones del campo colombiano para superar las causas histricas del conflicto, especialmente la desigualdad en el acceso a la tierra y el abandono del campo por parte del Estado.
    
        .bg-color-5.p-5.tarjeta(data-aos="fade-left")
          p.mb-0 La Reforma Rural Integral se fundamenta en una serie de principios que orientan todas sus acciones y polticas. Entre ellos se encuentran: el desarrollo integral del campo, la igualdad y el enfoque de gnero, el bienestar y el buen vivir, la priorizacin de las poblaciones ms afectadas, la integralidad de las acciones del Estado, el restablecimiento de derechos, la regularizacin de la propiedad, el derecho a la alimentacin, la participacin de las comunidades rurales, la medicin de impactos, el desarrollo sostenible, la presencia estatal y la democratizacin del acceso y uso adecuado de la tierra. Entre los ejes centrales de esta reforma, se destacan:
    
    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Acceso y uso de la tierra
    
    .bg-color-18.mb-5(data-aos="fade-up")
